@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

/* Custom scrollbar styles */
.scrollbar-hide {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.scrollbar-hide::-webkit-scrollbar {
  display: none;
}

/* Custom animations */
@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fade-in-delay {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  50% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes pulse-glow {
  0%, 100% {
    box-shadow: 0 0 5px rgba(239, 68, 68, 0.5);
  }
  50% {
    box-shadow: 0 0 20px rgba(239, 68, 68, 0.8), 0 0 30px rgba(59, 130, 246, 0.8);
  }
}

.animate-fade-in {
  animation: fade-in 1s ease-out;
}

.animate-fade-in-delay {
  animation: fade-in-delay 2s ease-out;
}

.animate-pulse-glow {
  animation: pulse-glow 2s infinite;
}

/* Streaming SuperBuddy hover effects */
.streaming-buddy {
  transition: all 0.3s ease;
}

.streaming-buddy:hover {
  transform: scale(1.1) rotate(5deg);
  filter: drop-shadow(0 0 10px rgba(255, 255, 255, 0.3));
}

/* Enhanced card hover effects */
.content-card {
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.content-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
}

/* Platform color themes */
.netflix-theme {
  background: linear-gradient(135deg, #E50914 0%, #B20710 100%);
}

.disney-theme {
  background: linear-gradient(135deg, #113CCF 0%, #0E2A8C 100%);
}

.prime-theme {
  background: linear-gradient(135deg, #00A8E1 0%, #007BB8 100%);
}

.hbo-theme {
  background: linear-gradient(135deg, #8B5A96 0%, #6B4373 100%);
}

.apple-theme {
  background: linear-gradient(135deg, #1D1D1F 0%, #000000 100%);
}

.crunchyroll-theme {
  background: linear-gradient(135deg, #FF6600 0%, #CC5200 100%);
}

/* Responsive grid for content */
@media (max-width: 768px) {
  .content-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 768px) {
  .content-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (min-width: 1024px) {
  .content-grid {
    grid-template-columns: repeat(6, 1fr);
  }
}

/* Custom gradient text */
.gradient-text {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Loading spinner for content */
.loading-spinner {
  border: 2px solid #1f2937;
  border-top: 2px solid #3b82f6;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Smooth transitions for all interactive elements */
button, .interactive {
  transition: all 0.2s ease-in-out;
}

button:hover {
  transform: translateY(-1px);
}

button:active {
  transform: translateY(0);
}

/* Modal backdrop blur */
.backdrop-blur {
  backdrop-filter: blur(10px);
}

/* Hero section overlay */
.hero-overlay {
  background: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.9) 0%,
    rgba(0, 0, 0, 0.6) 50%,
    rgba(0, 0, 0, 0.3) 100%
  );
}

/* Subscription management styles */
.subscription-card {
  background: linear-gradient(135deg, rgba(55, 65, 81, 0.8) 0%, rgba(31, 41, 55, 0.8) 100%);
  border: 1px solid rgba(75, 85, 99, 0.3);
  backdrop-filter: blur(10px);
}

.subscription-card:hover {
  border-color: rgba(75, 85, 99, 0.6);
  transform: translateY(-2px);
}

/* Platform indicator dots */
.platform-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  display: inline-block;
}

/* Search input enhancements */
.search-input {
  background: rgba(31, 41, 55, 0.8);
  border: 1px solid rgba(75, 85, 99, 0.3);
  backdrop-filter: blur(10px);
}

.search-input:focus {
  border-color: rgba(59, 130, 246, 0.5);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

/* Watchlist empty state */
.empty-state {
  background: linear-gradient(135deg, rgba(31, 41, 55, 0.5) 0%, rgba(17, 24, 39, 0.5) 100%);
  border: 2px dashed rgba(75, 85, 99, 0.3);
}

/* Content rating badge */
.rating-badge {
  background: linear-gradient(135deg, #fbbf24 0%, #f59e0b 100%);
  color: #1f2937;
  font-weight: 600;
}

/* Profile dropdown animations */
.profile-dropdown {
  animation: fadeInDown 0.3s ease-out;
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translate3d(0, -20px, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

/* Mobile responsiveness */
@media (max-width: 640px) {
  .hero-title {
    font-size: 2.5rem;
  }
  
  .content-card {
    width: 140px;
  }
  
  .content-card img {
    height: 200px;
  }
}

/* Dark mode optimizations */
:root {
  color-scheme: dark;
}

body {
  background-color: #000000;
  color: #ffffff;
}

/* Enhanced focus states for accessibility */
button:focus,
input:focus {
  outline: 2px solid #3b82f6;
  outline-offset: 2px;
}

/* Smooth scroll behavior */
html {
  scroll-behavior: smooth;
}